#ilab-imgix-preview-image {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%, -50%);
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.ilab-imgix-parameters-container {
  position: absolute;
  left:0px; right:0px; top:0px; bottom: 0px;
  padding-right: 20px;
  padding-left:10px;
  margin-right: 4px;
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 0px !important;
  overflow: scroll;
  padding-bottom:  0px !important;
}
.ilab-imgix-parameters-container select {
  font-size: 12px;
}

.ilab-imgix-parameters-container::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}
.ilab-imgix-parameters-container::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0,0,0,.5);
}

.ilab-imgix-parameter {
  padding:0px !important;
  margin:0px !important;
  margin-bottom: 15px !important;
  border-bottom: 1px solid rgba(0,0,0,0.125);
}

.ilab-imgix-parameter:first-child {
  margin-top:15px !important;
}

.ilab-imgix-parameter:last-child {
  border-bottom: 0px none;
  margin-bottom: 30px !important;
}

.ilab-imgix-param-title {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
  align-items: baseline;
  margin-bottom: 0px;
}

.ilab-imgix-param-title-colortype {

  -webkit-align-items: center !important;
  align-items: center !important;
}

.ilab-imgix-param-title-left {
  -webkit-flex: 1 50%;
  flex: 1 50%;
}

.ilab-imgix-param-title-right {
  -webkit-flex: 1 50%;
  flex: 1 50%;
  padding-left: 40px;
  text-align:right;
}

.ilab-imgix-param-title .wp-picker-input-wrap {
  display: none;
}

.ilab-imgix-param-title-right .wp-picker-container {
  position: relative;
}

.ilab-imgix-param-title-right .wp-picker-holder {
  position: absolute;
  right: 0px;
  top: 20px;
  z-index: 1000;
}

.ilab-imgix-param-title-right .wp-color-result {
  margin: 0 0px 8px 0px;
  height: 20px;
}

.ilab-imgix-param-title-right .wp-color-result::after {
  line-height:20px;
  font-size:10px;
}

.ilab-imgix-param-title-right .iris-picker {
  box-shadow: 0px 0px 8px rgba(0,0,0,0.5);
}

.ilab-imgix-param-title-right h3 {
  text-align: right;
}

.ilab-imgix-param-title h3, .imgix-param-blend-mode h3 {
  margin-top:0px;
  font-size: 11px;
  text-transform: uppercase;
  color: #666;
}

.ilab-imgix-media-param-title .ilab-imgix-param-title-left {
  -webkit-flex: 2 80%;
  flex:2 80%;
}

.ilab-imgix-media-param-title .ilab-imgix-param-title-right {
  -webkit-flex:1 20%;
  flex:1 20%;
}

.ilab-imgix-media-param-title .ilab-imgix-param-title-right a {
  text-align:center !important;
}

.imgix-param-blend-mode h3 {
  -webkit-flex: 1 50%;
  flex: 1 50%;
}

.imgix-param-blend-mode {
  margin-top: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
  align-items: baseline;
}

.imgix-param-blend-mode select {
  -webkit-flex: 2 100%;
  flex: 2 100%;
}

.ilab-imgix-parameters-container h4 {
  margin-left:-20px;
  margin-right:-15px;
  margin-bottom:15px;
  margin-top: 0px;
  font-size: 10px;
  text-transform: uppercase;
  color: #999;
  background-color: #ddd;
  padding: 5px;
  padding-left: 20px;
}

.ilab-imgix-parameter input[type=range]{
  display:block;
  width: 100%;
  -webkit-appearance: none;
  margin: 0 0 10px 0;
  background: none;
  padding:0px !important;
}

.ilab-imgix-parameter input[type=range]:focus {
  outline: none;
}

.ilab-imgix-parameter input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: inset 1px 1px 2px 0px rgba(0,0,0,0.25);
  background: #d4cfd4;
  border-radius: 4px;
  border: 0px solid #000101;
}

.ilab-imgix-parameter input[type=range]::-webkit-slider-thumb {
  border: 1px solid rgba(0,0,0,0.25);
  box-shadow: inset 0px 2px 2px 0px rgba(255,255,255,0.5);
  height: 17px;
  width: 17px;
  border-radius: 9px;
  background: #dcdcdc;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -6px;
}

.ilab-imgix-parameter input[type=range]:focus::-webkit-slider-runnable-track {
  background: #FFFFFF;
}

.ilab-imgix-parameter input[type=range]::-moz-range-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: inset 1px 1px 2px 0px rgba(0,0,0,0.25);
  background: #d4cfd4;
  border-radius: 4px;
  border: 0px solid #000101;
}

.ilab-imgix-parameter input[type=range]::-moz-range-thumb {
  border: 1px solid rgba(0,0,0,0.25);
  box-shadow: inset 0px 2px 2px 0px rgba(255,255,255,0.5);
  height: 17px;
  width: 17px;
  border-radius: 9px;
  background: #dcdcdc;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -6px;
}

.ilab-imgix-parameter a.imgix-param-reset {
  font-size: 11px;
  display:block;
  width: 100%;
  text-align:right;
  font-style: italic;
  margin-bottom: 10px;
  outline: none !important;
  border:0px none !important;
  text-decoration: none;
}
.ilab-imgix-parameter a:focus {
  outline: none !important;
  border:0px none !important;
}

.ilab-apply-now-wrapper {
  background:#fff;
  position: absolute;
  height: 30px;
  left:0px; right:0px; bottom:0px;
  padding: 15px;
  border-top: 1px solid #ddd;
}

#ilab-preview-wait-modal {
  position:absolute;
  box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
  text-align:center;
  padding: 20px 40px;
  border-radius: 10px;
  background-color: rgba(255,255,255,0.66);
  left:50%;
  top:50%;
  margin-left:-60px;
  margin-top:-32px;
}

#ilab-preview-wait-modal h3 {
  text-transform: uppercase;
  font-size: 13px;
}

#ilab-preview-wait-modal span.spinner {
  float: none !important;
}


.ilab-imgix-media-preview {
  position:relative;
  padding:0px !important;
  margin: 0px !important;
  padding-bottom:100% !important;
  background-image: url(../img/ilab-imgix-edit-bg.png);
}

.ilab-imgix-media-preview-inner {
  position:absolute;
  left:0px; right:0px; bottom:0px; top:0px;
  display: -webkite-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  justify-content: center;
  -webkit-justify-content: center;
}

.ilab-imgix-media-preview img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%, -50%);
  max-width: 100%;
  max-height: 100%;
  display: block;

}

.ilab-imgix-alignment-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: row;
  -webkit-flex-wrap: wrap;
  -webkit-justify-content:space-around;
  -webkit-align-items:baseline;
  flex: row;
  flex-wrap: wrap;
  justify-content:space-around;
  align-items:baseline;
  padding:0px 35px;
}

.imgix-alignment-button {
  background-color: #ddd;
  display: block;
  width: 60px;
  height: 60px;
  margin: 5px 5px;
  text-decoration:none;
  border-radius: 4px;
  border:1px solid #888;

  box-shadow: inset 0 1px 0 #FFFFFF, 0 1px 0 rgba(0, 0, 0, 0.15) !important;
}

.imgix-alignment-button.selected-alignment {
  background-color: #bbb;
  box-shadow: inset 1px 1px 1px rgba(0,0,0,0.25), 0 1px 0 rgba(0, 0, 0, 0.15) !important;
}

.ilab-modal-pillbox {
  padding: 10px 0px 20px 0px;
  text-align: center;
}

.ilab-imgix-pill {
  line-height:30px;
  font-size: 10px;

  padding: 10px 14px 10px 35px;
  background-color: #eaeaea;
  text-transform: uppercase;
  font-weight: bold;
  color: #444;
  text-decoration: none;
  border-radius: 8px;

  margin:3px;
}

.ilab-imgix-pill.pill-selected {
  background-color: #ccc;
  box-shadow: inset 1px 1px 1px rgba(0,0,0,0.25);
  color: white;

}

.ilab-imgix-pill-enhance {
  background-image: url(../img/ilab-imgix-magic-wand-black.png);
  background-repeat: no-repeat;
  background-position: 12px 8px;
  padding-left: 40px;
  background-size: 20px 20px;
}


.ilab-imgix-pill-enhance.pill-selected {
  background-image: url(../img/ilab-imgix-magic-wand-white.png);
}


.ilab-imgix-pill-redeye {
  background-image: url(../img/ilab-imgix-red-eye-black.png);
  background-repeat: no-repeat;
  background-position: 12px 8px;
  padding-left: 40px;
  background-size: 20px 20px;
}

.ilab-imgix-pill-redeye.pill-selected {
  background-image: url(../img/ilab-imgix-red-eye-white.png);
}



.ilab-modal-bottom-bar a {
  margin-left: 10px !important;
}


.ilab-modal-bottom-bar select {
  font-size: 13px !important;
  min-width: 140px;
  margin-left: 10px !important;
}


.ilab-modal-bottom-bar label {
  font-size: 13px !important;
}

#imgix-preset-make-default-container {

  -ms-flex-align: center;
  -webkit-align-items: center;
  -webkit-box-align: center;

  align-items: center;
  display: -webkit-flex;
  display: flex;
  min-height:30px;
}

#imgix-preset-container {

  -ms-flex-align: center;
  -webkit-align-items: center;
  -webkit-box-align: center;

  align-items: center;
  display: -webkit-flex;
  display: flex;
}

#imgix-status-container {
  display: -webkit-flex;
  display: flex;
  flex:1;
  -webkit-flex:1;
