.imgix-image-size-label {

  margin-top: 3px;
  line-height: 32px;
  padding: 0px 5px 0px 15px;
  margin-right: 3px;
  font-size: 11px;
  text-transform: uppercase;
  color: #888;
  font-weight: bold;
  cursor: pointer !important;
}

.imgix-image-size-select {
  margin-top: 4px;
